import React, {useState, useEffect, createRef} from 'react';
import {connect} from 'react-redux';
import './images.scss'
import Paper from '@material-ui/core/Paper';
import backend from '../../../services/backend'
import Dropzone from 'react-dropzone'
import { Link } from "react-router-dom";
import Spinner from "../../../components/spinner";
import DeleteIcon from '@material-ui/icons/Delete';
import classNames from "classnames";
import Checkbox from "@material-ui/core/Checkbox";
import Button from "@material-ui/core/Button";


export default function Images (props) {

  const [selectedImages, setSelectedImages] = useState({});

  function onDeleteClick() {
    let confirm = window.confirm(`Confirm deleting images`);
    if (!confirm) return;

    let images = Object.entries(selectedImages)
      .filter(entry => entry[1])
      .map(entry => entry[0]);


    props.onImageDelete(images);
    setSelectedImages({});
  }

  return (
    <div className="Images">
      <h2>Layer images</h2>
      {props.isLoading && <Spinner position="absolute"/>}
      <div>
        {props.data.images && props.data.images.map((image) => (
          <div className="image-container" key={image._id}>
            <div
              className="image-wrapper"
              onClick={() => setSelectedImages({...selectedImages, [image._id]: !selectedImages[image._id]})}
            >
              <img src={image.value} alt=""/>
              <div className={ classNames('checkbox-wrapper', { 'visible': selectedImages[image._id] }) }>
                <Checkbox
                  checked={selectedImages[image._id] ? 'checked' : false}
                  color="primary"
                />
              </div>
            </div>
          </div>
        ))}
        <p>
          {
            Object.values(selectedImages).find(el => el) && (
              <Button
                variant="contained"
                color="secondary"
                onClick={onDeleteClick}
                disabled={props.isLoading}
              >Delete</Button>
            )
          }
        </p>
      </div>
      <Dropzone multiple={true} onDrop={acceptedFiles => props.onImageSubmit(acceptedFiles)}>
        {({getRootProps, getInputProps}) => (
          <div className="container">
            <div className="dropzone" {...getRootProps()}>
              <input {...getInputProps()} />
              <p>Drag 'n' drop some files here, or click to select files</p>
            </div>
          </div>
        )}
      </Dropzone>
    </div>
  )
}
