import React, {useState, useEffect, createRef} from 'react';
import {connect} from 'react-redux';
import './layer-items.scss'
import Paper from '@material-ui/core/Paper';
import backend from '../../services/backend'
import Dropzone from 'react-dropzone'
import { Link } from "react-router-dom";
import Spinner from "../../components/spinner";
import Colors from './colors';
import Images from './images'
import Form from './form'
import Button from '@material-ui/core/Button';
import {toast} from "react-toastify";
import {onError} from "../../services/error-helpers";

export default function LayerItems (props) {

  const [layerData, setLayerData] = useState({});
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    setIsLoading(true);
    backend.getLayerItems(props.match.params.setId, props.match.params.id)
      .then((response) => {
        setLayerData(response.data);
      })
      .catch(onError)
      .finally(() => setIsLoading(false))
  }, [props.match.params]);

  function onImageSubmit(acceptedFiles) {
    let filesToSend = acceptedFiles;
    setIsLoading(true);
    backend.postLayerItemsImages(props.match.params.setId, props.match.params.id, filesToSend)
      .then((response) => {
        toast.success('Images were successfully added');
        setLayerData(response.data);
      })
      .catch(onError)
      .finally(() => setIsLoading(false))
  }

  function onImageDelete(images) {
    setIsLoading(true);
    backend.deleteLayerItemImage(props.match.params.setId, props.match.params.id, images)
      .then((response) => {
        toast.info('Images were successfully deleted');
        setLayerData(response.data);
      })
      .catch(onError)
      .finally(() => setIsLoading(false))
  }

  function onPatchLayer(data) {
    return backend.patchLayerItem(props.match.params.setId, props.match.params.id, data)
      .then(() => {})
      .catch(onError)
  }

  return (
    <Paper className='Paper-main LayerItems'>
      <Button color='primary' onClick={props.history.goBack}>Back to Layers</Button>
      <div className="form-container">
        <Form
          match={props.match}
          data={layerData}
          onSubmit={onPatchLayer}
        />
      </div>
      <Colors
        match={props.match}
        data={layerData}
        setLayerData={setLayerData}
      />
      <Images
        data={layerData}
        onImageDelete={onImageDelete}
        onImageSubmit={onImageSubmit}
        isLoading={isLoading}
      />
    </Paper>
  )
}
