import React, {useState, useEffect, createRef} from 'react';
import {connect} from 'react-redux';
import './colors.scss'
import Paper from '@material-ui/core/Paper';
import backend from '../../../services/backend'
import { Link } from "react-router-dom";
import Spinner from "../../../components/spinner";
import ColorPicker from '../../../components/material-ui-color-picker'
import {Formik} from "formik";
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add'
import DeleteIcon from '@material-ui/icons/Delete';
import {toast} from "react-toastify";
import {onError} from "../../../services/error-helpers";
import Checkbox from "@material-ui/core/Checkbox";
import classNames from 'classnames';

export default function Colors (props) {

  const [newColor, setNewColor] = useState('#ffffff');
  const [selectedColors, setSelectedColors] = useState({});
  const [isLoading, setIsLoading] = useState(false);

  function onDeleteColors() {
    let confirm = window.confirm(`Confirm deleting colors`);
    if (!confirm) return;

    setIsLoading(true);

    let colors = Object.entries(selectedColors)
      .filter(entry => entry[1])
      .map(entry => entry[0]);

    backend.deleteLayerItemColor(props.match.params.setId, props.match.params.id, colors)
      .then(response => {
        props.setLayerData(response.data);
        toast.info('Colors were successfully deleted');
        setSelectedColors({});
      })
      .catch(onError)
      .finally(() => setIsLoading(false));
  }

  return (
    <div className="Colors">
      <h2>Colors</h2>
      {props.data && props.data.colors && props.data.colors.map(color => (
       <div
         key={color}
         className="color"
         style={{background: color}}
         onClick={() => setSelectedColors({...selectedColors, [color]: !selectedColors[color]})}
       >
         <div className={ classNames('checkbox-wrapper', { 'visible': selectedColors[color] }) }>
           <Checkbox
             checked={selectedColors[color] ? 'checked' : false}
             color="primary"
           />
         </div>
       </div>
      ))}
      <p>
        {
          Object.values(selectedColors).find(el => el) && (
            <Button
              variant="contained"
              color="secondary"
              onClick={onDeleteColors}
              disabled={isLoading}
            >Delete</Button>
          )
        }
      </p>
      <Formik
        enableReinitialize
        initialValues={{color: '#ffffff'}}
        onSubmit={(values, { setSubmitting }) => {
          backend.postLayerItemColor(props.match.params.setId, props.match.params.id, newColor)
            .then(response => {
              toast.success(`Color ${newColor} was successfully added`);
              props.setLayerData(response.data)
            })
            .catch(onError)
            .finally(() => setSubmitting(false))
        }}
      >
        {props => {
          const {
            values,
            errors,
            touched,
            isSubmitting,
            handleChange,
            handleSubmit,
          } = props;
          return (
            <form onSubmit={handleSubmit}>
              <p>
                <ColorPicker
                  label="Add new color"
                  id='color'
                  name='color'
                  value={newColor}
                  onChange={val => setNewColor(val)}
                />
              </p>
              <p>
                <Button variant="contained" type="submit" color="primary" disabled={isSubmitting}>
                  <AddIcon /> Add color
                </Button>
              </p>
            </form>
          )
        }}
      </Formik>
    </div>
  )
}
