import axios from 'axios';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:3001/';

let instance = null;

class Backend {
  authAxios = null;
  filesAxios = null;
  token = null;

  constructor() {
    if (!instance) {
      instance = this;
      let token = localStorage.getItem('token');
      if (token) {
        this.token = token;
      }
      this.setAxiosInstance(token);
    }
    return instance;
  }

  // AUTH

  login(data) {
    return new Promise((resolve, reject) => {
      axios.post(API_URL + 'auth/login', data)
        .then(response => {
          this.setAxiosInstance(response.data.token);
          localStorage.setItem('token', response.data.token);
          resolve(response);
        })
        .catch(e => reject(e));
    });
  }


  // SETS

  getSets() {
    return this.request('get', 'admin/sets');
  }

  postSet(data) {
    return this.request('post', 'admin/sets', data);
  }

  deleteSet(setId) {
    return this.request('delete', 'admin/sets/' + setId);
  }


  // LAYERS

  getLayers(setId) {
    return this.request('get', 'admin/' + setId + '/layers');
  }

  addLayer(setId, data) {
    return this.request('post', 'admin/' + setId + '/layers/', data);
  }

  deleteLayer(setId, id) {
    return this.request('delete', 'admin/' + setId + '/layers/' + id);
  }

  getLayerItems(setId, id) {
    return this.request('get', 'admin/' + setId + '/layers/' + id);
  }

  patchLayerItem(setId, id, data) {
    return this.request('patch', 'admin/' + setId + '/layers/' + id, data);
  }

  // COLORS

  postLayerItemColor(setId, id, color) {
    return this.request('post', 'admin/' + setId + '/layers/' + id + '/colors', {color});
  }

  deleteLayerItemColor(setId, id, colors) {
    // color = color.replace('#', '');
    return this.request('delete', 'admin/' + setId + '/layers/' + id + '/colors/', { data: { colors } });
  }

  // IMAGES

  postLayerItemsImages(setId, id, filesToSend) {
    let data = new FormData();
    filesToSend.forEach(file => data.append('images', file));
    data.append('name', 'images');

    return this.filesAxios.post(API_URL + 'admin/' + setId + '/layers/' + id + '/images', data);
  }

  deleteLayerItemImage(setId, id, images) {
    return this.request('delete', 'admin/' + setId + '/layers/' + id + '/images/', { data: { images }});
  }

  // common methods

  request(method, ...args) {
    return new Promise((resolve, reject) => {
      this.authAxios[method](...args).then(response => {
        resolve(response);
      })
      .catch(e => {
        if (e.response && e.response.status === 401) {
          window.location = (process.env.REACT_APP_ROUTER_BASE_URL || '') + '/#/login';
        }
        reject(e);
      })
    })
  }

  // SET INSTANCE
  setAxiosInstance(token) {
    this.authAxios = axios.create({
      baseURL: API_URL,
      headers: {
        Authorization: 'jwt ' + token,
        'Content-Type': 'application/json',
      }
    });

    this.filesAxios = axios.create({
      baseURL: API_URL,
      headers: {
        Authorization: 'jwt ' + token,
        'Content-Type': 'multipart/form-data'
      }
    });
  }
}

export default new Backend();
